@import url(https://fonts.googleapis.com/css?family=Lato|Roboto);
:root {
  --main-color: #222431;
  --secondary-color: #272C39;
  --modalThird-color: #39445c;
  --vSite-color: #608cac;
  --vGit-color:  #3c82e7;
  --vMore-color: #e74c3c;
  --textMain-color: #E0E0E0;
  --textProject-color: #E0E0E0;
  --backTop-color : #222431;
  --projBorder-color: rgba(255, 255, 255, 0.075)
}


body {
  margin: 0;
  padding: 0;
  height: 100%;
  background-color: #222431;
  background-color: var(--main-color);
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
  overflow-x: hidden;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#inroContainer {
    height: 100vh;
    margin: 0 auto;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
  }
  
  #introText {
    color: var(--textMain-color);
    text-align: center;
    border-left: 13px solid var(--textMain-color);
    padding: 4em;
  }
  
  #introText h1,
  #introText h2,
  #projectsContainer h1 {
    font-family: 'Roboto';
  }
  
  #introText h1 {
    letter-spacing: 10px;
    font-size: 3.5em;
    margin: 0;
  }
  
  #introText h2 {
    letter-spacing: 5px;
    font-size: 2em;
    padding-top: 5px;
  }
  
  #contactBar {
    padding-top: 5px;
  }
  
  #contactBar a {
    text-decoration: none;
    color: var(--textMain-color);
    opacity: 0.7;
  }
  
  #contactBar svg {
    font-size: 3em;
    padding: 3px;
    margin-right: 10px;
    cursor: pointer;
    -webkit-transition: 0.3s all;
    transition: 0.3s all;
  }
  
  #contactBar svg:hover,
  #arrowDown svg:hover {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
    -webkit-transform-origin: center;
            transform-origin: center;
    color: var(--vMore-color);
  }
  
  #arrowDown {
    position: absolute;
    bottom: 10px;
  }
  
  #arrowDown svg {
    font-size: 2.4em;
    color: var(--textMain-color);
    -webkit-transition: 0.3s all;
    transition: 0.3s all;
    cursor: pointer;
  }

  @media only screen and (max-width: 768px) {
    #introText {
      border-left: 0px;
      padding: 0px;
    }

    #introText h1 {
      letter-spacing: 5px;
      font-size: 3em;
    }

    #introText h2 {
      letter-spacing: 3px;
      font-size: 1.3em;
    }

    #contactBar svg, 
    #arrowDown svg {
      font-size: 1.4em;
    }

    .colorBarContainer {
      grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
      top: 10px;
      left: auto
    }

    #arrowDown {
      bottom: 5px;
    }
  }
  
.colorBarContainer {
  display: grid;
  grid-gap: 10px;
  background-color: black;
  border-radius: 10px;
  padding: 10px;
  position: absolute;
  left: 10px;
}

.colorBarItem {
  height: 25px;
  width: 25px;
  border-radius: 50%;
  cursor: pointer;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}

.colorBarItem:hover {
 border-radius: 30%;
}

.darkTheme {
    background: #222431;
  }

.lightTheme {
  background: #ffffff;
}

.yellowTheme {
    background: #f9ed69;
}

.redTheme {
    background: #e23e57;
}

.blueTheme {
    background: #3ec1d3;
}

.orangeTheme {
    background: #ff8f56;
}

.greenTheme {
    background: #20716a;
}

.violetTheme {
    background: #5d50c6;
}
#projectsContainer {
  background-color: var(--secondary-color);
  width: 100%;
  height: 100%;
}

#projectsContainer h1 {
  text-align: center;
  padding-top: 1.5em;
  opacity: 0.75;
  font-size: 1.4em;
  color: var(--textProject-color);
  letter-spacing: 1.4px;
}

#projectGridContainer {
  padding-top: 1.5em;
  padding-bottom: 3em;
  display: grid;
  /* grid-template-columns: 1fr 1fr; */
  grid-gap: 4em;
  width: 95%;
  margin: auto;
}

.projectGridItem {
  width: 100%;
  margin: auto;
  display: grid;
  grid-template-rows: auto;
  grid-template-columns: 1fr 1fr;
  border-radius: 4px;
  border: 1px solid transparent;
  -webkit-transition: 0.3s cubic-bezier(0.17, 0.67, 0.83, 0.67);
  transition: 0.3s cubic-bezier(0.17, 0.67, 0.83, 0.67);
  /* padding-bottom: 30px; */
}

.projectGridItem:hover {
  /* border: 1px solid var(--projBorder-color); */
  box-shadow: 0 2px 6px 0 hsla(0, 0%, 0%, 0.2);
}

.projectImageBackground {
  width: 100%;
  height: 400px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  justify-content: center;
  align-items: center;
}

.projectInformation {
  background-color: #e0e0e0;
  width: 100%;
  height: 90%;
}

.projectTitle {
  margin: auto;
  margin-top: 16px;
}

.projectTitle h2 {
  /* text-align: center; */
  font-family: "Lato";
  color: var(--textProject-color);
  opacity: 0.88;
  letter-spacing: 1.5px;
  font-weight: 700;
  margin: 0px;
  margin-left: 24px;
  font-size: 1.1em;
}

.projectDesc {
  margin: 1em;
}

.projectDesc p {
  text-align: left;
  font-family: "Roboto";
  color: var(--textProject-color);
  opacity: 0.80;
  line-height: 24px;
  font-weight: 200;
  font-size: 1em;
}

.projectImage {
  background-color: gray;
  width: 100%;
  height: 100%;
  margin: auto;
  background-size: cover;
  background-position: center;
  margin-left: 0px;
  grid-row-end: span 3;
  border-radius: 4px 0px 0px 4px;
}

.buttonGrid {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-row-start: 3;
  grid-column-start: 2;
  width: 100%;
  font-size: 12px;
  margin: auto;
  margin-bottom: 16px;
}

.visitSiteButton {
  height: 100%;
  width: 100%;
  /* background-color: green; */
  display: flex;
  justify-content: center;
  align-items: center;
  margin: auto;
}

.visitSiteButton a {
  color: white;
  text-decoration: none;
  font-family: "Lato";
  font-weight: bolder;
  letter-spacing: 2px;
  background-color: var(--vSite-color);
  border: 2px solid var(--vSite-color);
  text-transform: uppercase;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  border-radius: 4px;
  padding: 10px;
  text-align: center;
}

.visitGithubButton {
  height: 100%;
  width: 100%;
  /* background-color: black; */
  display: flex;
  justify-content: center;
  align-items: center;
  margin: auto;
}

.visitGithubButton a {
  color: white;
  text-decoration: none;
  font-family: "Lato";
  font-weight: bolder;
  letter-spacing: 2px;
  background-color: var(--vGit-color);
  border: 2px solid var(--vGit-color);
  text-transform: uppercase;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  border-radius: 4px;
  padding: 10px;
}

.moreInfoButton {
  height: 100%;
  width: 100%;
  /* background-color: black; */
  display: flex;
  justify-content: center;
  align-items: center;
  margin: auto;
  cursor: pointer;
}

.moreInfoButton .infoButtonA {
  color: white;
  text-decoration: none;
  font-family: "Lato";
  font-weight: bolder;
  letter-spacing: 2px;
  background-color: var(--vMore-color);
  border: 2px solid var(--vMore-color);
  text-transform: uppercase;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  border-radius: 4px;
  padding: 10px;
}

.visitSiteButton a:hover {
  color: var(--vSite-color);
  background-color: transparent;
  
}

.visitGithubButton a:hover {
  color: var(--vGit-color);
  background-color: transparent;
  
}

.moreInfoButton a:hover {
  color: var(--vMore-color);
  background-color: transparent;
  
}

@media only screen and (max-width: 1024px) {
  #projectGridContainer {
    grid-template-columns: 1fr;
  }

  .projectGridItem {
    grid-template-columns: 1fr;
    grid-template-rows: auto;
  }

  .projectTitle {
    margin: 1em;
    margin-top: 16px;
  }

  .projectImage {
    grid-row-start: 2;
    grid-row-end: 2;
    border-radius: 4px;
    height: 400px;
    width: auto;
    margin: 1em;
  }

  .projectDesc {
    grid-row-start: 3;
  }

  .buttonGrid {
    grid-column-start: 1;
    grid-row-start: 4;
    margin: auto;
    margin-bottom: 16px;
  }
}

@media only screen and (max-width: 768px) {
  .projectTitle h2 {
    margin: auto;
    font-size: 1em;
    letter-spacing: 0.5px;
  }

  .visitSiteButton a,
  .visitGithubButton a,
  .infoButtonA {
    padding: 7px !important;
    font-size: 10px;
    letter-spacing: 0.8px !important;
  }
}

.modal {
  position: fixed; /* Stay in place */
  z-index: 1; /* Sit on top */
  left: 0;
  top: 0;
  width: 100%; /* Full width */
  height: 100%; /* Full height */
  background-color: rgb(0, 0, 0); /* Fallback color */
  background-color: rgba(0, 0, 0, 0.4); /* Black w/ opacity */
}

.modalBody {
  height: 100%;
  width: 100%;
  position: absolute;
  z-index: -1;
}
/* Modal Content */
.modal-content {
  background-color: var(--main-color);
  
  margin: auto;
  /* padding: 20px; */
  width: 93%;
  height: 100%;
  display: grid;
  grid-template-rows: 12% 88%;
  cursor: auto;
  box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.25);
}

/* The Close Button */
.close p {
  color: var(--textMain-color);
  float: right;
  font-size: 1.75em;
  font-weight: bold;
  -webkit-transition: 0.3s cubic-bezier(0.17, 0.67, 0.83, 0.67);
  transition: 0.3s cubic-bezier(0.17, 0.67, 0.83, 0.67);
}

.close p:hover,
.close p:focus {
  color: var(--vMore-color);
  text-decoration: none;
  cursor: pointer;
}

.topBar {
  background-color: var(--main-color);
  display: grid;
  grid-template-columns: 95% 5%;
}

.topBar h2,
.topBar span {
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
}

.topBar h2 {
  color: var(--textMain-color);
  text-align: center;
  font-family: "Lato";
  letter-spacing: 1.5px;
  font-size: 1.3em;
  margin: 0px;
}

.midModal {
  height: 100%;
  overflow-y: scroll;
  scroll-behavior: smooth;
  background-color: var(--secondary-color);
}

.contentContainer {
  background-color: var(--modalThird-color);
  font-family: "Roboto";
  text-align: left;
  color: white;
  margin: 1em;
  padding: 1em;
  /* height: 100%; */
  border-radius: 10px;
  /* border: 1px solid #D0D0D0; */
  display: grid;
  grid-template-rows: auto;
  grid-gap: 2.5em;
}

.modalProjectImage {
  width: 100%;
  height: 500px;
  margin: auto;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  margin-left: 0px;
  grid-row-end: span 3;
  border-radius: 10px;
}

.modalDescription {
  width: 90%;
  margin: auto;
  font-size: 1.2em;
  line-height: 30px;
  letter-spacing: 0.5px;
  color: var(--textProject-color);
}

.bottomBar {
  margin: 1em;
  border-radius: 10px;
}

.modalSwitchContainer {
  width: 100%;
  height: 100%;
  display: grid;
  grid-template-columns: 9fr 1fr 9fr;
  grid-gap: 1em;
}

.backTopSwitch a {
  -webkit-transform: scale(2);
          transform: scale(2);
  color: var(--backTop-color);
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}

.backTopSwitch a:hover {
  color: var(--vSite-color)
}

.previousSwitch,
.nextSwitch {
  width: 100%;
  height: 100%;
  margin: auto;
  font-family: "Lato";
  font-size: 1.5em;
  text-align: center;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  color: white;
  display: grid;
}

.previousSwitch .buttonArrowIcon,
.nextSwitch .buttonArrowIcon,
.backTopSwitch {
  display: flex;
  align-items: center;
  justify-content: center;
}

.previousSwitch {
  background-color: var(--vGit-color);
  border-radius: 10px;
  grid-template-columns: 10% 90%;
}

.nextSwitch {
  background-color: var(--vMore-color);
  border-radius: 10px;
  grid-template-columns: 90% 10%;
}

.previousSwitch p,
.nextSwitch p {
  margin: 0px;
  margin-top: 8px;
  margin-bottom: 8px;
}

.previousSwitch:hover,
.nextSwitch:hover {
  -webkit-filter: brightness(1.2);
          filter: brightness(1.2);
  cursor: pointer;
}

.previousSwitch:hover {
  color: white;
}

.nextSwitch:hover {
  color: white;
}

@media only screen and (max-width: 768px) {
  .modal-content {
    width: 100%;
  }
  
  .topBar {
    grid-template-columns: 90% 10%;
  }

  .topBar h2 {
    font-size: 1em;
    letter-spacing: 0.5px;
  }

  .modalProjectImage {
    height: 200px;
  }

  .modalSwitchContainer {
    grid-template-columns: 9fr 4fr 9fr;
  }
  
  .previousSwitch p,
  .nextSwitch p {
    font-size: 0.8em;
  }

  .previousSwitch {
    grid-template-columns: 20% 80%;
  }

  .nextSwitch {
    grid-template-columns: 80% 20%;
  }

  .previousSwitch .buttonArrowIcon,
  .nextSwitch .buttonArrowIcon {
    font-size: 0.7em;
  }
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

